"use client";

import React, { useState, useEffect } from "react";
import { Users } from "lucide-react";
import { apiRequest } from "@/app/authservice/api";
import { toast } from "react-hot-toast";

import Header from "../../components/patients/Header";
import PatientCard from "../../components/patients/PatientCard";
import PatientSearch from "../../components/patients/PatientSearch";
import PatientTable from "../../components/patients/PatientTable";
import PatienUpdatetModal from "../../components/patients/PatientModel";
import PatientDeleteModal from "../../components/patients/PatientDeleteModel";

const PatientsPage = () => {
  const [searchTerm, setSearchTerm] = useState("");
  const [patients, setPatients] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedPatient, setSelectedPatient] = useState(null);

  /* =========================
     FETCH PATIENTS (with optional search)
     ========================= */
  const fetchPatients = async (query = "") => {
    try {
      const endpoint = query
        ? `/patients?search=${encodeURIComponent(query)}`
        : "/patients";
      const res = await apiRequest(endpoint, "GET");

      if (res?.success) {
        setPatients(res.data || []);
      } else {
        toast.error(res?.message || "Failed to fetch patients");
      }
    } catch (error) {
      console.error("Failed to fetch patients", error);
      toast.error("Failed to load patients");
    }
  };

  useEffect(() => {
    fetchPatients();
  }, []);

  /* =========================
     HANDLERS
     ========================= */
  const handleAddClick = () => {
    setSelectedPatient(null);
    setIsModalOpen(true);
  };

  const handleEditClick = (patient) => {
    setSelectedPatient(patient);
    setIsModalOpen(true);
  };

  const handleDeletePatient = async (patient) => {
    const id = patient._id || patient.patientId;

    if (!id) {
      toast.error("Patient ID not found");
      return;
    }

    if (!confirm(`Delete patient "${patient.fullName}"?`)) return;

    try {
      const res = await apiRequest(`/patients/deletePatient/${id}`, "DELETE");

      if (res?.success) {
        toast.success("Patient deleted successfully");
        fetchPatients(searchTerm); // refresh list
      } else {
        toast.error(res?.message || "Failed to delete patient");
      }
    } catch (error) {
      console.error("Delete failed", error);
      toast.error("Failed to delete patient");
    }
  };

  const handleAddPatient = () => {
    setIsModalOpen(false);
    fetchPatients(searchTerm);
  };

  const handleEditPatient = () => {
    setIsModalOpen(false);
    fetchPatients(searchTerm);
  };

  const handleSearchChange = (e) => {
    const value = e.target.value;
    setSearchTerm(value);
    fetchPatients(value); // fetch filtered patients from API
  };

  return (
    <main className="space-y-6">
      <Header onAdd={handleAddClick} />

      <PatientCard
        title="Total Patients"
        count={patients.length}
        icon={<Users className="w-6 h-6 text-white" />}
      />

      <PatientSearch
        value={searchTerm}
        onChange={handleSearchChange}
      />

      <div className="flex-1 min-w-0">
        <PatientTable
          patients={patients}
          onEdit={handleEditClick}
          onDelete={handleDeletePatient}
        />
      </div>

      {isModalOpen && (
        <PatientModal
          patient={selectedPatient}
          onCreate={handleAddPatient}
          onUpdate={handleEditPatient}
          onClose={() => setIsModalOpen(false)}
        />
      )}
    </main>
  );
};

export default PatientsPage;
